// An highlighted block
<template>
	<view class="content">
		<view class="form">
			<view class="inputWrapper">
				<view class="inputTip">
					<text>用户名：</text>
				</view>
				<view class="input">
					<input class="baseInput" type="text" v-model="userName" @focus="inputFocus(userName)"
						@blur="inputBlur(userName)" />
				</view>
			</view>

			<view class="inputWrapper">
				<view class="inputTip">
					<text>性别：</text>
				</view>
				<view class="input">
					<picker @change="sexIndex = $event.detail.value" :value="sexIndex" :range="sex"
						class="baseInput picker">
						<text>{{ sex[sexIndex] }}</text>
					</picker>
				</view>
			</view>

			<view class="inputWrapper">
				<view class="inputTip">
					<text>绑定手机：</text>
				</view>
				<view class="input">
					<input class="baseInput" :type="telType" v-model="tel" :disabled="telDisabled"
						@focus="inputFocus(tel)" @blur="inputBlur(tel)" />
				</view>
			</view>

			<view class="inputWrapper">
				<view class="inputTip">
					<text>绑定邮箱：</text>
				</view>
				<view class="input">
					<input class="baseInput" type="text" v-model="email" :disabled="emailDisabled"
						@focus="inputFocus(email)" @blur="inputBlur(email)" />
				</view>
			</view>
			<view class="inputWrapper">
				<view class="inputTip">
					<text>修改密码：</text>
				</view>
				<view class="input">
					<input class="baseInput" type="password" v-model="password" @focus="inputFocus(password)"
						@blur="inputBlur(password)" />
				</view>
			</view>

			<view class="inputWrapper">
				<view class="inputTip">
					<text>确认密码：</text>
				</view>
				<view class="input">
					<input class="baseInput" type="password" v-model="passwordAgain"
						@focus="inputFocus(passwordAgain + 'again')" @blur="inputBlur(passwordAgain + 'again')" />
				</view>
			</view>

			<view class="inputWrapper">
				<view class="inputTip">
					<text>验证码：</text>
				</view>
				<view class="input">
					<input class="baseInput" type="number" v-model="checkCode" @focus="inputFocus(checkCode)"
						@blur="inputBlur(checkCode)" />
					<view class="seconds" @click="sendCheckCode">
						<text>{{ seconds ? seconds : "发送" }}</text>
					</view>
				</view>
			</view>
			<view class="btn" @click="updateUserInfo">
				<text>完成修改</text>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		testUserInfo
	} from "@/api/userInfo/userInfo.js";
	import {
		updateUserInfo
	} from "@/api/userInfo";
	export default {
		data() {
			return {
				account: "",
				password: "",
				passwordAgain: "",
				checkCode: "",
				userName: "",
				sex: ["男", "女"],
				sexIndex: 0,
				email: "",
				tel: "",
				seconds: 0,
				telDisabled: false,
				emailDisabled: false,
				passwordDisabled: false,
				telType: "text",
			};
		},
		onLoad({
			userName,
			userSex,
			email,
			tel
		}) {
			this.account = uni.getStorageSync("account");
			this.password = uni.getStorageSync("password");
			this.passwordAgain = uni.getStorageSync("password");
			this.userName = userName || '小白的猫';
			this.sexIndex = userSex || 0;
			this.telDisabled = !!Number(tel);
			this.emailDisabled = !!email;
			this.email = email ? email : "未绑定邮箱";
			this.tel = Number(tel) ? tel : "未绑定手机号";
		},
		methods: {
			inputFocus(value) {
				switch (value) {
					case this.userName:
						if (this.userName !== "小白的猫") break;
						this.userName = "";
						break;
					case this.tel:
						if (this.tel !== "未绑定手机号") break;
						this.tel = "";
						this.telType = "number";
						break;
					case this.email:
						if (this.email !== "未绑定邮箱") break;
						this.email = "";
						break;
					case this.password:
						if (this.password !== uni.getStorageSync("password")) break;
						this.password = "";
						break;
					case this.passwordAgain + "again":
						if (this.passwordAgain !== uni.getStorageSync("password")) break;
						this.passwordAgain = "";
						break;
					case this.checkCode:
						this.checkCode = "";
						break;
				}
			},
			inputBlur(value) {
				switch (value) {
					case this.userName:
						this.userName = this.userName || "小白的猫";
						break;
					case this.tel:
						this.telType = "text";
						this.tel = this.tel || "未绑定手机号";
						break;
					case this.email:
						this.email = this.email || "未绑定邮箱";
						break;
					case this.password:
						this.password = this.password || uni.getStorageSync("password");
						break;
					case this.passwordAgain + "again":
						this.passwordAgain = this.passwordAgain || uni.getStorageSync("password");
						break;
					case this.checkCode:
						this.checkCode = this.checkCode || "";
						break;
				}
			},
			updateUserInfo() {
				// 检查输入信息
				if (
					!testUserInfo({
						userName: this.userName,
						tel: this.tel,
						email: this.email,
						password: this.password,
						passwordAgain: this.passwordAgain,
						checkCode: this.checkCode
					})
				)
					return;

				// 提交修改信息
				updateUserInfo({
					userName: this.userName,
					userSex: this.sexIndex,
					tel: this.tel,
					email: this.email,
					password: this.password,
					checkCode: this.checkCode,
					account: this.account,
					success() {
						// 修改成功
						setTimeout(() => {
							uni.switchTab({
								url: `/pages/tabbar/user/user?userName=${this.userName}&userSex=${this.sexIndex}`
							})
						}, 500)
					}
				})
			},
		},
	};
</script>

<style lang="less">
	@import "@/static/css/base.less";

	.content {
		padding: 10rpx 0;
	}

	.input {
		position: relative;
		color: #666;

		.picker {
			display: flex;
			justify-content: center;
			align-items: center;
		}

		.seconds {
			position: absolute;
			top: 0;
			right: 4%;
			padding: 20rpx 30rpx;
			color: #888;
		}
	}
</style>
